.cover {
  width: 100%;
  height: 350px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

}

.cover:before {
  content: "";
  background: linear-gradient(to bottom, black, transparent);
  position: absolute;
  top: 0;
  left: 0;
  height: 30%;
  width: 100%;
}
.cover__title {
  z-index:1;
  text-align: center;
  word-break: keep-all;
  padding: 1em;
}

.bg--black {
  background-color: var(--dark-bg-color);
}

.bg--blue {
  background-image: linear-gradient(225deg, rgb(18, 28, 133) 0%, rgb(35, 48, 165) 29%, rgb(18, 75, 138) 73%, rgb(18, 82, 104) 100%);
}

.bg--purple {
  background-image: linear-gradient(to right top, rgb(20, 52, 110) 0%, rgb(47, 58, 102) 25%, rgb(108, 45, 134) 100%);
}

.bg--green {
  background-image: linear-gradient(to right top, rgb(77, 61, 99) 0%, rgb(30, 97, 105) 50%, rgb(26, 104, 72) 100%)
}

.bg--red {
  background-image: linear-gradient(215deg, rgb(158, 36, 57) 0%, rgb(132, 40, 74) 30%, rgb(111, 43, 87) 100%);
}

.bg--gray {
  background-image: linear-gradient(229deg, rgb(65, 91, 103) 0%, rgb(47, 74, 90) 27%, rgb(62, 74, 104) 72%, rgb(74, 80, 102) 100%);
}

.button--circle {
  display: none;
  position: absolute;
  top: 50px;
  left: 10px;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background: var(--gray-200);
  opacity: 0.7;
}

.arrow {
  border: solid var(--dark-font-color);
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;

  &--right {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
  }

  &--left {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
  }

  &--up {
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
  }

  &--down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }

}


